import {
	codeLogin,
	saveWXAuthInfo,
	phoneLogin
} from "@/api/login"

import {
	API_RETURN_CODE,
	STORAGE_KEYS
} from "@/common/constant.js"
import {
	setToken
} from '@/common/token.js'

import $config from "@/common/config.js"

import store from "@/store/index.js"

// 
export const getLoginCode = () => {
	return new Promise((resolve, reject) => {
		uni.login({
			provider: 'weixin',
			success: res => {
				resolve(res)
			},
			fail: err => {
				reject(err)
			}
		});
	})
}

//  userInfo
export const getUserProfile = () => {
	return new Promise((resolve, reject) => {
		uni.getUserProfile({
			desc: '',
			success: (info_res) => {
				resolve(info_res)
			},
			fail: err => {
				reject(err)
			}
		})
	})
}

//  getPhoneNubmberreject
export const getPhoneNumber = (res) => {
	return new Promise((resolve, reject) => {
		console.log(res);
		const {
			detail
		} = res;
		// 1 0
		if (detail.errMsg.indexOf('ok') !== -1) {
			resolve(res)
		} else if (detail.errMsg.indexOf('fail') !== -1) {
			reject(detail.errMsg)
		}
	})
}

// 2.code
export const wxLogin = (loginRes) => {
	return new Promise((resolve, reject) => {
		const {
			errMsg,
			code
		} = loginRes
		if (errMsg.indexOf('ok') !== -1) {
			codeLogin({
				code,
				app_key: $config.app.app_key,
				app_secret: $config.app.app_secret
			}).then(res => {
				const {
					code,
					msg,
					data
				} = res

				if (code == API_RETURN_CODE.SUCCESS.code) {
					console.log('res.data', res.data)
					store.commit('login', res.data);
				} else {
					uni.showToast({
						title: msg,
						duration: 2000
					})
				}
				resolve(res.data)
			}).catch((err) => {
				console.log(err);
				reject(err)
			})
		} else if (errMsg.indexOf('fail') !== -1) {
			uni.showToast({
				title: 'wx.login ',
				duration: 2000
			})
			reject()
		}
	})
}

